body {
  margin: 0;
  font-family: "Lato";
  text-decoration: none;
}

.cointenerMenu{
    background: #131313;
    width: 20%;
    text-align: center;
    padding: 20px;
    height: auto;
    box-shadow: 5px 0px black;
}
.logoMenu{
    margin-top:20px;
    width: 250px;
}
.cointenerButtons{
    margin-top: 40px;
    text-decoration: none;
}
.buttonMenu{
    color: white;
    margin-bottom:30px;
    font-family: "Lato";
    font-size: 18px;
}
.cointenerMain {
    background-color: #131313;
    display: flex;
}
.titleMain{
    color: cornsilk;
    padding: 40px;
    font-size: 20px;
    font-family: "Lato";
}
.cointenerAbout{
    background: #131313;
    display: flex;
}
.titleAbout{
    font-family: "Lato";
    font-size: 30px;
    padding: 60px;
    color: cornsilk;
}
.cointenerWorks{
    background: #131313;
    display: flex;
}
.titleWorks{
    font-family: "Lato";
    font-size: 40px;
    color: cornsilk;
    padding: 80px;
}
.cointenerContact{
    background: #131313;
    display: flex;
}
.titleContact{
    font-family: "Lato";
    font-size: 50px;
    padding:100px;
    color: cornsilk;
}
.spanContact{
    color: #00FA77;
}
